@import '../../theme/colors.scss';

.container {
  white-space: pre-line;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  width: 100vw;
}

.content {
  max-width: 362px;
  min-width: 320px;
  padding: 18px;
}

.steps {
  font-size: 12px;
  line-height: 15px;
  color: color(passiveText);
  margin-top: 20px;
  margin-bottom: 8px;
}

.column {
  display: flex;
  flex-direction: column;
  margin-top: 8px;
}
