@import '../../theme/colors.scss';

.container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.button {
  background-color: color(green);
  height: 48px;
  border-radius: 24px;
  width: 140px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-top: 24px;
}

.button_text {
  font-weight: 900;
  font-size: 16px;
  line-height: 20px;
  color: color(white);
}
